@import url(https://fonts.googleapis.com/css2?family=PT+Sans:wght@400;700&display=swap);
.header{margin:86px 0;text-align:center;font-size:48px;font-weight:700;line-height:1.35;letter-spacing:normal}@media screen and (min-width: 320px){.header{margin:63px 0;font-size:38px}}

.add-form{margin:0 auto;position:relative}.add-form__input{width:100%;padding:14px 22px;font-size:17px;text-align:left;border:solid 1px rgba(0,0,0,0.5);box-sizing:border-box;border-radius:23px}.add-form__input:focus{outline:none}.add-form__btn{position:absolute;top:-2px;right:-2px;border:none;cursor:pointer;background:none}.add-form__btn img{height:56px}.add-form__btn:focus{outline:none}@media screen and (min-width: 320px){.add-form{width:295px}}

.tracker-list{margin:26px auto 0;font-size:17px;font-weight:700;letter-spacing:0.43px}.tracker-list .tracker{display:-webkit-flex;display:flex;padding:25px 0;-webkit-justify-content:space-between;justify-content:space-between;border-top:1px solid rgba(0,0,0,0.1)}.tracker-list .tracker.active{color:#3faf6c;background-color:#fdfdf6}.tracker-list .tracker__name{margin:0 25px}.tracker-list .tracker__time{float:left;margin-right:21px}.tracker-list .tracker__btn{margin:0 8px;width:24px;height:24px;border:none;cursor:pointer;background:none}.tracker-list .tracker__btn:focus{outline:none}.tracker-list .tracker:last-child{border-bottom:1px solid rgba(0,0,0,0.1)}@media screen and (max-width: 559px){.tracker-list .tracker__name{margin:0 12px;overflow:hidden;white-space:nowrap}.tracker-list .tracker__period{min-width:150px;text-align:right}}

*{margin:0;padding:0;font-family:'PT Sans', sans-serif}@media screen and (min-width: 560px){.container{width:558px;margin:0 auto}}@media screen and (max-width: 559px){.container{min-width:320px;margin:0 auto}}

